.social-select__icon {
  display: none;
}

.social-select__header {
  display: none;
}

@include big-mobile {
  .social-select {
    margin: 0 auto;
    position: relative;
    transform: translateX(-10px);
  }

  .social-select.is-active .social-select__body {
    display: block;
  }

  .social-select__header {
    cursor: pointer;
    display: flex;
  }

  .social-select__current {
    padding: 8px;
  }

  .social-select__icon {
    align-items: center;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    width: 20px;
    height: 20px;
    transform: translate(-5px, 12px);

    & svg {
      width: 7px;
      height: 5.5px;
    }

    & use {
      fill: #D6B27E;
    }
  }

  .social-select__body {
    border-top-right-radius: 23px;
    border-bottom-right-radius: 23px;
    border-bottom-left-radius: 23px;
    background-color: var(--light-color);
    border: 2px solid var(--bg-color-one);
    display: none;
    left: 0;
    position: absolute;
    right: 0;
    top: 100%;

  }

  .social-select__item {
    cursor: pointer;
    font-size: 16px;
    line-height: 24px;
    padding: 8px;
  }

  .social-select__item:hover {
    background-color: #f2f2f2;
  }
}
