.project {
  padding-bottom: 137px;
  position: relative;

  @include big-desktop {
    padding-bottom: 215px;
  }

  @include tablet {
    padding-bottom: 110px;
  }

  @include mobile {
    padding-bottom: 70px;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0%;
    right: -91%;
    background-image: url(../img/project-bg.png);
    background-repeat: no-repeat;
    background-size: auto;
    background-position: 0 0;
    height: 100%;
    width: 100%;
    transform: translateX(-50%);
    z-index: -2;

    @include tablet {
      display: none;
    }
  }

  &::after {
    content: '';
    position: absolute;
    top: 34%;
    left: 50%;
    background-image: url(../img/project-line-bottom.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    max-width: 2405px;
    height: 976px;
    width: 100%;
    transform: translateX(-50%);
    z-index: -2;
  }

  & .title {
    margin-left: 55px;
    margin-bottom: 123px;

    @include big-desktop {
      margin-left: 65px;
    }

    @include tablet {
      margin-left: 24px;
      margin-bottom: 43px;
    }

    @include mobile {
      margin-left: 0;
      margin-bottom: 24px;
    }
  }

  & .subtitle {
    background-color: var(--bg-color-one);
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    border-bottom-left-radius: 40px;
    padding: 40px 67px;
    margin-bottom: 25px;

    @include tablet {
      padding: 27px 45px;
      border-top-right-radius: 23px;
      border-bottom-right-radius: 23px;
      border-bottom-left-radius: 23px;
    }

    @include mobile {
      padding: 27px 15px;
      margin-bottom: 22px;
    }
  }

  &__list {}

  &__item {
    background-color: var(--light-color);
    border: 2px solid var(--bg-color-one);
    box-shadow: 10px 10px 50px rgba(41, 35, 37, 0.1);
    max-width: 1070px;
    width: 100%;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    border-bottom-left-radius: 40px;

    @include tablet {
      border-top-right-radius: 23px;
      border-bottom-right-radius: 23px;
      border-bottom-left-radius: 23px;
    }

    & .text {
      margin-bottom: 15px;

      @include mobile {
        margin-bottom: 20px;
      }

      &:first-child {
        padding-left: 65px;
      }
    }

    & .mb-57 {
      margin-bottom: 57px;

      @include tablet {
        margin-bottom: 42px;
      }

      @include mobile {
        margin-bottom: 25px;
      }
    }

    &:first-child {
      margin-left: 20px;
      margin-bottom: 140px;
      position: relative;

      @include big-desktop {
        margin-left: 10px;
        margin-bottom: 90px;
      }

      @include medium-desktop {
        margin-left: auto;
        margin-right: auto;
      }

      @include mobile {
        margin-bottom: 35px;
      }

      &::before {
        content: '';
        position: absolute;
        top: -20px;
        left: -18px;
        width: 36px;
        height: 36px;
        background-image: url(../img/circle.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 0 0;
        z-index: -1;

        @include medium-desktop {
          display: none;
        }
      }
    }

    &:nth-child(2) {
      margin-left: 120px;
      margin-bottom: 110px;
      border-top-right-radius: 0;
      border-top-left-radius: 40px;
      position: relative;

      @include big-desktop {
        margin-left: 110px;
        margin-bottom: 90px;
      }

      @include medium-desktop {
        margin-left: auto;
        margin-right: auto;
      }

      @include mobile {
        margin-bottom: 30px;
      }

      &::before {
        content: '';
        position: absolute;
        top: -20px;
        right: -18px;
        width: 36px;
        height: 36px;
        background-image: url(../img/circle.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 0 0;
        z-index: -1;

        @include medium-desktop {
          display: none;
        }
      }

      &.project__item--color .subtitle {
        border-top-right-radius: 0;
        border-top-left-radius: 40px;

        @include tablet {
        border-top-left-radius: 23px;
        }
      }
    }

    &:nth-child(3) {
      margin-left: 20px;
      margin-bottom: 47px;
      position: relative;

      @include big-desktop {
        margin-left: 10px;
        margin-bottom: 90px;
      }

      @include medium-desktop {
        margin-left: auto;
        margin-right: auto;
      }

      @include mobile {
        margin-bottom: 30px;
      }

      &::before {
        content: '';
        position: absolute;
        top: -20px;
        left: -18px;
        width: 36px;
        height: 36px;
        background-image: url(../img/circle.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 0 0;
        z-index: -1;

        @include medium-desktop {
          display: none;
        }
      }
    }

    &:nth-child(4) {
      margin-left: 90px;
      border-top-right-radius: 0;
      border-top-left-radius: 40px;
      position: relative;

      @include big-desktop {
        margin-left: 110px;
      }

      @include medium-desktop {
        margin-left: auto;
        margin-right: auto;
      }

      &::before {
        content: '';
        position: absolute;
        top: -20px;
        right: -18px;
        width: 36px;
        height: 36px;
        background-image: url(../img/circle.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 0 0;
        z-index: -1;

        @include medium-desktop {
          display: none;
        }
      }

      &::after {
        content: '';
        position: absolute;
        top: -71%;
        left: 16%;
        background-image: url(../img/project-bottom-bg.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 0 0;
        width: 119%;
        height: 126%;
        transform: translateX(-50%);
        z-index: -2;

        @include tablet {
          display: none;
        }
      }

      &.project__item--color .subtitle {
        border-top-right-radius: 0;
        border-top-left-radius: 40px;
      }
    }
  }

  &__item--color {
    border: 2px solid var(--bg-color-two);

    .subtitle {
      background-color: var(--bg-color-two);
    }
  }

  &__item-list {
    padding-left: 65px;
    padding-right: 85px;
    padding-bottom: 65px;

    @include tablet {
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 33px;
    }

    @include mobile {
      padding-left: 16px;
      padding-right: 16px;
      padding-bottom: 10px;
    }
  }

  &__item-item {
    position: relative;
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    stroke: var(--primary-color);
    padding-left: 50px;
    margin-bottom: 24px;

    @include tablet {
      font-size: 18px;
      line-height: 32px;
    }

    @include mobile {
      font-size: 16px;
      line-height: 28px;
      padding-left: 33px;
      margin-bottom: 18px;
    }
  }

  &__item-svg {
    fill: none;
    stroke: var(--primary-color);
    stroke-width: 3px;
    position: absolute;
    top: 7px;
    left: 0;
    width: 25px;
    height: 25px;

    @include mobile {
      top: 0;
    }
  }

  &__swiper {
    margin-top: 40px;

    @include tablet {
      margin-top: 20px;
    }
  }
}
