.decore {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../img/line.svg);
  background-repeat: no-repeat;
  background-size: 1542px 9411px;
  background-position: center center;
  top: 122px;
  left: -23px;
  overflow-y: auto;
  overflow-x: hidden;
  text-align: center;
  z-index: -1;

  @include big-desktop {
    background-image: url(../img/line-small.svg);
    top: 1.1%;
    left: 8px;
  }

  @include medium-desktop {
    display: none;
  }
}
