.hero {
  padding-top: 192px;
  padding-bottom: 245px;
  position: relative;

  @include big-desktop {
    padding-top: 160px;
    padding-bottom: 120px;
  }

  @include tablet {
    padding-bottom: 127px;
  }

  @include mobile {
    padding-top: 75px;
    padding-bottom: 67px;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    background-image: url(../img/hero-bg.png);
    background-repeat: no-repeat;
    background-size: 73%;
    background-position: 0 0;
    max-width: 1920px;
    height: 100%;
    width: 100%;
    transform: translateX(-22%);
    z-index: -3;

    @include tablet {
      background-image: url(../img/hero-small-bg.png);
      background-size: 100% 1074px;
      max-width: 100%;
      left: 0;
      transform: translateX(0);
    }
  }

  &::after {
    content: '';
    position: absolute;
    bottom: 16.5%;
    left: 50%;
    background-image: url(../img/hero-line.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    max-width: 1920px;
    height: 714px;
    width: 100%;
    transform: translateX(-50%);
    z-index: -2;

    @include big-desktop {
      bottom: 21.5%;
      max-width: 1366px;
      height: 507px;
    }

    @include tablet {
      max-width: 767px;
      height: 222px;
      bottom: 3.5%;
    }
  }

  &__container {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: -85px;
      left: 0;
      background-image: linear-gradient(90.89deg, #FFFFFF 24.5%, rgba(255, 255, 255, 0) 99.5%);
      width: 75%;
      height: 110%;
      z-index: -1;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;
  }

  &__title {
    font-family: 'Helvetica', sans-serif;
    font-weight: 700;
    font-size: 80px;
    line-height: 97px;
    text-transform: uppercase;
    color: var(--primary-color);
    margin: 0;
    margin-left: 17px;
    margin-bottom: 33px;

    @include medium-desktop {
      font-size: 65px;
    }

    @include tablet {
      font-size: 50px;
      line-height: 61px;
      margin-left: 0;
      margin-bottom: 5px;
    }

    @include mobile {
      font-size: 30px;
      line-height: 36px;
    }
  }

  &__text {
    font-family: 'Helvetica', sans-serif;
    font-weight: 700;
    font-size: 90px;
    line-height: 109px;
    text-transform: uppercase;
    -webkit-text-stroke: 2px #39453b;
    -webkit-text-fill-color: transparent;
    margin: 0;
    display: block;
    transform: translate(70px, -47px);

    @include medium-desktop {
      font-size: 73px;
      transform: translate(70px, -60px);
    }

    @include tablet {
      font-size: 55px;
      line-height: 67px;
      transform: translate(62px, -35px);
    }

    @include big-mobile {
      transform: translate(14px, -35px);
    }

    @include mobile {
      font-size: 30px;
      line-height: 36px;
      transform: translate(14px, -17px);
    }
  }

  &__list {
    max-width: 560px;
    width: 100%;

    @include tablet {
      max-width: 100%;
    }
  }

  &__item {
    padding-left: 90px;

    @include big-desktop {
      padding-left: 60px;
    }

    @include tablet {
      padding-left: 73px;
    }

    @include mobile {
      padding-left: 40px;
    }

    &:not(:last-child) {
      margin-bottom: 65px;

      @include tablet {
        margin-bottom: 57px;
      }

      @include mobile {
        margin-bottom: 30px;
      }
    }
  }

  &__item-title {
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 20px;
    position: relative;

    @include tablet {
      font-size: 20px;
      line-height: 24px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 10px;
    }

    &::before {
      content: '';
      position: absolute;
      top: -5px;
      left: -70px;
      width: 42px;
      height: 42px;
      background-image: url(../img/hero-decore.svg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 0 0;

      @include mobile {
        width: 25px;
        height: 25px;
        top: -2px;
        left: -39px;
      }
    }
  }

  &__item-text {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    color: var(--primary-color);
    margin: 0;

    @include tablet {
      font-size: 18px;
      line-height: 32px;
    }

    @include mobile {
      font-size: 16px;
      line-height: 28px
    }
  }

  &__image {
    max-width: 1050px;
    width: 100%;
    height: 970px;
    position: absolute;
    right: -30%;
    top: -14%;
    z-index: -2;

    @include big-desktop {
      max-width: 924px;
      height: 840px;
      right: -19%;
      top: -12%;
    }

    @include tablet {
      display: none;
    }
  }
}
