.about {
  padding-bottom: 100px;
  position: relative;

  @include tablet {
    padding-bottom: 88px;
  }

  @include mobile {
    padding-bottom: 45px;
  }

  &::after {
    content: '';
    position: absolute;
    top: -23%;
    left: 50%;
    background-image: url(../img/about-bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    max-width: 2405px;
    height: 977px;
    width: 100%;
    transform: translateX(-50%);
    z-index: -2;
  }

  &::before {
    content: '';
    position: absolute;
    bottom: -9%;
    left: 50%;
    background-image: url(../img/about-bottom-bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    max-width: 2405px;
    height: 976px;
    width: 100%;
    transform: translateX(-50%);
    z-index: -2;
  }

  &__me {
    max-width: 700px;
    width: 100%;
    margin-left: auto;
    margin-right: -30px;
    margin-bottom: 52px;

    @include big-desktop {
      margin-right: 0;
    }

    @include medium-desktop {
      margin-left: 26px;
    }

    @include mobile {
      margin-left: 0;
      margin-bottom: 26px;
    }
  }
}

.about-directions {
  background-color: var(--bg-color-two);
  box-shadow: 10px 10px 50px rgba(41, 35, 37, 0.1);
  max-width: 1070px;
  width: 100%;
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
  border-bottom-left-radius: 40px;
  margin-left: auto;
  margin-right: -90px;
  margin-bottom: 90px;
  padding: 35px;
  position: relative;

  @include big-desktop {
    max-width: 1050px;
    margin-right: -30px;
  }

  @include medium-desktop {
    margin-right: auto;
  }

  @include tablet {
    padding: 35px 22px;
    margin-bottom: 65px;
  }

  @include mobile {
    padding: 35px 18px;
  }

  &::before {
    content: '';
    position: absolute;
    top: -20px;
    left: -18px;
    width: 36px;
    height: 36px;
    background-image: url(../img/circle.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    z-index: -1;

    @include medium-desktop {
      display: none;
    }
  }

  &__item {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    color: var(--light-color);
    padding-left: 50px;
    position: relative;

    @include tablet {
      font-size: 18px;
      line-height: 30px;
    }

    @include mobile {
      padding-left: 33px;
      font-size: 16px;
      line-height: 28px;
    }

    &:not(:last-child) {
      margin-bottom: 28px;

      @include tablet {
        margin-bottom: 12px;
      }
    }
  }

  &__svg {
    width: 25px;
    height: 25px;
    fill: none;
    stroke: var(--light-color);
    stroke-width: 3px;
    position: absolute;
    top: 5px;
    left: 0;
  }
}

.about-significant {
  max-width: 945px;
  margin-left: auto;
  position: relative;

  @include big-desktop {
    max-width: 980px;
  }

  @include medium-desktop {
    margin-right: auto;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -60px;
    width: 36px;
    height: 36px;
    background-image: url(../img/circle.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    z-index: -1;

    @include tablet {
      left: 4px;
    }

    @include mobile {
      width: 25px;
      height: 25px;
      left: 0;
      top: 5px;
    }
  }

  & .subtitle {

    @include tablet {
      padding-left: 64px;
      margin-bottom: 30px;
    }

    @include mobile {
      padding-left: 40px;
      margin-bottom: 22px;
    }
  }

  &__list {
    margin-top: 35px;

    @include tablet {
      margin-top: 28px;
    }

    @include mobile {
      margin-top: 23px;
    }
  }

  &__item {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    margin-bottom: 20px;
    padding-left: 45px;
    position: relative;

    @include mobile {
      font-size: 15px;
      line-height: 28px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 8px;
      left: 0;
      width: 14px;
      height: 14px;
      background-color: var(--bg-color-one);
      border-radius: 50%;
    }
  }
}
