.contact {
  padding-bottom: 67px;

  @include big-desktop {
    padding-bottom: 43px;
  }

  @include mobile {
    padding-bottom: 32px;
  }

  &__title {
    font-family: 'Helvetica';
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
    text-transform: uppercase;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 103px;
    padding-left: 120px;
    position: relative;

    @include tablet {
      font-size: 28px;
      line-height: 36px;
      padding-left: 77px;
      margin-bottom: 34px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 36px;
      padding-left: 40px;
    }

    &::before {
      content: '';
      position: absolute;
      top: -29px;
      left: 22px;
      width: 71px;
      height: 71px;
      background-image: url(../img/title-finish.svg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 0 0;

      @include tablet {
        width: 61px;
        height: 60px;
        top: -21px;
        left: 3px;
      }

      @include mobile {
        width: 36px;
        height: 35px;
        top: 0;
        left: 0;
      }
    }
  }
}

.contact-form {
  transform: translateX(17.5%);
  background: var(--bg-form);
  box-shadow: 10px 10px 50px rgba(41, 35, 37, 0.1);
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
  border-bottom-left-radius: 40px;
  padding: 37px 65px 60px 65px;
  max-width: 1073px;
  width: 100%;
  position: relative;

  @include big-desktop {
    transform: translateX(20%);
    max-width: 1020px;
    padding: 37px 45px 60px 45px;
  }

  @include medium-desktop {
    margin-left: auto;
    margin-right: auto;
    transform: translateX(0);
  }

  @include tablet {
    padding: 37px 23px 30px 23px;
  }

  @include big-mobile {
    display: flex;
    flex-direction: column;
    padding: 37px 23px 50px 23px;
  }

  @include mobile {
    padding: 37px 23px 70px 23px;
  }

  &::before {
    content: '';
    position: absolute;
    top: -20px;
    left: -18px;
    width: 36px;
    height: 36px;
    background-image: url(../img/circle.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    z-index: -1;

    @include medium-desktop {
      display: none;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    @include big-mobile {
      flex-direction: column;
    }
  }

  &__label {
    display: flex;
    flex-direction: column-reverse;
    flex: 1 0 auto;

    &:not(:last-child) {
      margin-right: 30px;

      @include big-mobile {
        margin-right: 0;
        margin-bottom: 20px;
        width: 100%;
      }

      @include mobile {
        margin-bottom: 15px;
      }
    }
  }

  &__input {
    max-width: 335px;
    width: 100%;
    background-color: var(--light-color);
    opacity: 0.7;
    border-radius: 10px;
    padding: 16px 25px;
    font-weight: 400;
    font-size: 16px;
    line-height: 1;
    color: var(--primary-color);

    @include tablet {
      max-width: 210px;
    }

    @include big-mobile {
      max-width: 100%;
    }

    &::placeholder {
      font-weight: 400;
      font-size: 16px;
      line-height: 36px;
      color: var(--bg-color-one);
    }
  }

  &__btn {
    background-color: var(--bg-color-one);
    border-radius: 10px;
    font-family: 'Helvetica';
    font-weight: 700;
    font-size: 24px;
    text-transform: uppercase;
    color: var(--light-color);
    padding: 10px 40px 16px;
    height: 54px;
    display: flex;
    align-items: flex-end;

    @include big-mobile {
      width: 93%;
      justify-content: center;
      position: absolute;
      left: 50%;
      bottom: 20px;
      transform: translateX(-50%);
    }

    @include mobile {
      width: 87%;
      font-size: 22px;
      line-height: 36px;
      align-items: center;
      bottom: 35px;
    }
  }

  &__text {
    font-family: 'Verdana';
    font-weight: 400;
    font-size: 10px;
    line-height: 16px;
    text-align: right;
    color: var(--bg-color-one);
    display: block;
    padding: 5px 0;

    @include big-mobile {
      order: 1;
      padding-bottom: 35px;
    }

    @include mobile {
      font-size: 10px;
      line-height: 16px;
      text-align: left;
    }
  }

  &__textarea {
    width: 100%;
    min-height: 90px;
    background-color: var(--light-color);
    opacity: 0.7;
    border-radius: 10px;
    padding: 10px 25px;
    font-weight: 400;
    font-size: 16px;
    color: var(--primary-color);
    resize: vertical;
    appearance: none;
    border: none;

    @include big-mobile {
      margin-bottom: 15px;
    }

    @include mobile {
      margin-bottom: 2px;
    }

    &::placeholder {
      font-weight: 400;
      font-size: 16px;
      line-height: 36px;
      color: var(--bg-color-one);
    }
  }
}

.just-validate-error-label {
  margin-bottom: 5px;

  @include mobile {
    font-size: 14px;
  }
}
