.header {
  position: absolute;
  top: 10px;
  left: 0;
  width: 100%;
  z-index: 1;

  @include tablet {
    top: 5px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__tel {}
}

.logo {

  &__image {
    width: 251px;
    height: 50px;

    @include tablet {
      margin-left: -15px;
    }

    @include mobile {
      width: 181px;
      height: 36px;
    }
  }
}

.social {
  display: flex;
  justify-content: space-between;

  &__item {
    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__link {

    svg {
      width: 25px;
      height: 25px;
      fill: #D6B27E;
    }
  }

  &__link--yt {

    svg {
      width: 30px;
      height: 25px;
    }
  }

  &__link--tg {

    svg {
      width: 29px;
      height: 25px;

      @include tablet {
        width: 23px;
        height: 20px;
      }
    }
  }

  &__link--wa {
    svg {
      width: 25px;
      height: 25px;

      @include tablet {
        width: 20px;
        height: 20px;
      }
    }
  }

  &__link--vb {
    svg {
      width: 26px;
      height: 29px;

      @include tablet {
        width: 21px;
        height: 22px;
      }
    }
  }
}

.header-tel {
  font-family: 'Helvetica', sans-serif;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  text-transform: uppercase;
  color: var(--primary-color);

  @include tablet {
    font-size: 24px;
    line-height: 29px;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__list {
    display: flex;
    justify-content: flex-end;
    margin-top: 5px;

    @include mobile {
      display: none;
    }
  }

  &__num {
    @include mobile {
      display: none;
    }
  }

  &__svg {
    display: none;
    width: 29px;
    height: 29px;
    fill: var(--bg-color-one);

    @include mobile {
      display: block;
    }
  }

  &__item {

    &:not(:last-child) {
      margin-right: 12px;
    }
  }

  &__link {
    fill: var(--bg-color-one);
  }
}
