.footer {
  background-color: var(--bg-color-one);
  padding-top: 12px;
  padding-bottom: 10px;

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;

    @include tablet {
      margin-bottom: 10px;

      & .logo__image {
        display: none;
      }
    }

    @include big-mobile {
      flex-wrap: wrap;

      & .logo__image {
        display: block;
        width: 181px;
        height: 36px;
      }
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include big-mobile {
      flex-direction: column;
      position: relative;

      & .copy {
        position: absolute;
        left: 0;
        bottom: 0;
      }
    }

    &-links {

      @include big-mobile {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: var(--light-color);
    transition: color 0.3s ease-in-out, opacity .3s ease-in-out;
    margin-left: 20px;

    @include big-mobile {
      margin-bottom: 5px;

      &:last-child {
        align-self: flex-end;
      }
    }

    &:hover {
      opacity: 0.7;
    }

    &-text {
      vertical-align: super;
    }
  }

  &__artix {
    fill: #FAFEFD;
    width: 20px;
    height: 21px;
    margin-left: 13px;
  }
}


.footer-social {
  display: flex;
  justify-content: space-between;
  margin-left: -40px;

  @include tablet {
    margin-left: 4px;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 30px;

      @include tablet {
        margin-right: 20px;
      }

      @include big-mobile {
        margin-right: 12px;
      }
    }
  }

  &__link {

    svg {
      width: 25px;
      height: 25px;
      fill: rgba(255, 255, 255, 0.5);

      @include big-mobile {
        width: 19px;
        height: 19px;
      }
    }
  }

  &__link--yt {

    svg {
      width: 30px;
      height: 25px;

      @include big-mobile {
        width: 23px;
        height: 19px;
      }
    }
  }

  &__link--vk {}

  &__link--tg {
    svg {
      width: 29px;
      height: 25px;

      @include big-mobile {
        width: 23px;
        height: 20px;
      }
    }
  }

  &__link--wa {
    svg {
      width: 25px;
      height: 25px;

      @include big-mobile {
        width: 20px;
        height: 20px;
      }
    }
  }

  &__link--vb {
    svg {
      width: 26px;
      height: 29px;

      @include big-mobile {
        width: 19px;
        height: 20px;
      }
    }
  }
}

.footer-nav {

  @include big-mobile {
    margin-top: 30px;
    margin-bottom: 10px;
  }

  &__list {
    display: flex;
    justify-content: space-between;

    @include tablet {
      transform: translateX(-25px);
    }

    @include big-mobile {
      transform: translateX(0);
    }
  }

  &__item {

    &:not(:last-child) {
      margin-right: 84px;

      @include tablet {
        margin-right: 50px;
      }

      @include big-mobile {
        margin-right: 40px;
      }
    }
  }

  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 36px;
    color: var(--light-color);

    @include tablet {
      font-size: 14px;
    }
  }
}

.footer-tel {

  &__list {
    display: flex;
    justify-content: flex-end;
    margin-top: 5px;
  }

  &__item {

    &:not(:last-child) {
      margin-right: 12px;
    }
  }

  &__link {
    fill: var(--light-color);
  }

  &__item-mob {
    display: none;
    font-family: 'Helvetica';
    font-weight: 700;
    font-size: 22px;
    line-height: 27px;
    text-transform: uppercase;
    color: var(--light-color);
    margin-right: 20px;


    @include big-mobile {
      display: inline-block;
    }
  }
}

.footer__bottom-links {

  @include big-desktop {
    & .offer {
      align-self: center;
    }
  }
}
