html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.title {
  font-family: 'Helvetica';
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
  text-transform: uppercase;
  color: var(--primary-color);
  margin: 0;
  margin-bottom: 42px;
  padding-left: 64px;
  position: relative;

  @include tablet {
    font-size: 28px;
    line-height: 36px;
    padding-left: 54px;
    margin-bottom: 34px;
  }

  @include mobile {
    font-size: 24px;
    line-height: 36px;
    padding-left: 40px;
    margin-bottom: 25px;
  }

  &::before {
    content: '';
    position: absolute;
    top: -34px;
    left: -35px;
    width: 51px;
    height: 79px;
    background-image: url(../img/title-map.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;

    @include tablet {
      width: 40px;
      height: 62px;
      top: -24px;
      left: -21px;
    }

    @include mobile {
      width: 25px;
      height: 39px;
      top: -2px;
      left: 0;
    }
  }
}

.subtitle {
  font-family: 'Helvetica';
  font-weight: 700;
  font-size: 28px;
  line-height: 36px;
  text-transform: uppercase;
  color: var(--primary-color);
  margin: 0;
  margin-bottom: 25px;

  @include tablet {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 18px;
  }

  @include mobile {
    font-size: 22px;
    line-height: 36px;
    margin-bottom: 15px;
  }

  &--color {
    color: var(--light-color);
  }
}

.text {
  font-weight: 400;
  font-size: 20px;
  line-height: 32px;
  margin: 0;

  @include tablet {
    font-size: 18px;
    line-height: 30px;
  }

  @include mobile {
    font-size: 16px;
    line-height: 28px;
  }

  &--color {
    color: var(--light-color);
  }
}

.ml-65 {
  margin-left: 65px;

  @include tablet {
    margin-left: 20px;
  }

  @include mobile {
    margin-left: 12px;
  }
}

.main {
  position: relative;
}

.d-n {
  display: none;
}
