.activities {
  padding-bottom: 155px;
  position: relative;

  @include tablet {
    padding-bottom: 130px;
  }

  @include mobile {
    padding-bottom: 57px;
  }

  &::after {
    content: '';
    position: absolute;
    top: 10px;
    left: 50%;
    background-image: url(../img/hero-line.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    max-width: 2405px;
    height: 976px;
    width: 100%;
    transform: translateX(-50%);
    z-index: -2;
  }

  & .title {
    margin-left: 55px;
    margin-bottom: 70px;

    @include big-desktop {
      margin-left: 65px;
      margin-bottom: 110px;
    }

    @include tablet {
      margin-left: 25px;
      margin-bottom: 72px;
    }

    @include mobile {
      margin-left: 0;
      margin-bottom: 60px;
    }
  }

  &__item {
    display: flex;

    &:not(:last-child) {
      margin-bottom: 65px;

      @include mobile {
        margin-bottom: 36px;
      }
    }

    &:nth-child(odd) {
      flex-direction: row-reverse;
      position: relative;

      @include medium-desktop {
        flex-direction: row;
      }

      @include mobile {
        flex-direction: column;
      }

      &::before {
        content: '';
        position: absolute;
        top: 10px;
        left: 49.5%;
        width: 36px;
        height: 36px;
        background-image: url(../img/circle-color.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 0 0;
        z-index: -1;

        @include medium-desktop {
          left: 0;
        }

        @include mobile {
          width: 25px;
          height: 25px;
          top: 0px;
        }
      }

      & .activities__item-date {
        margin-right: 50px;

        @include medium-desktop {
          margin-left: 70px;
          margin-right: 60px;
        }

        @include tablet {
          margin-left: 85px;
          margin-right: 65px;
        }

        @include mobile {
          font-size: 38px;
          line-height: 36px;
          margin-left: 40px;
          margin-right: 0;
          margin-bottom: 5px;
        }
      }
    }

    &:nth-child(even) {
      position: relative;

      @include mobile {
        flex-direction: column;
      }

      &::before {
        content: '';
        position: absolute;
        top: 10px;
        right: 48%;
        width: 36px;
        height: 36px;
        background-image: url(../img/circle.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 0 0;
        z-index: -1;

        @include medium-desktop {
          left: 0;
        }

        @include mobile {
          width: 25px;
          height: 25px;
          top: 0;
        }
      }

      & .activities__item-date {
        margin-left: 50px;

        @include medium-desktop {
          margin-left: 70px;
          margin-right: 60px;
        }

        @include tablet {
          margin-left: 85px;
          margin-right: 65px;
        }

        @include mobile {
          font-size: 38px;
          line-height: 36px;
          margin-left: 40px;
          margin-right: 0;
          margin-bottom: 5px;
        }
      }
    }
  }

  &__item-text {
    max-width: 370px;
    width: 100%;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: #1A1A1A;
    margin: 0;

    @include medium-desktop {
      order: 1;
      max-width: 100%;
    }

    @include mobile {
      font-size: 16px;
      line-height: 28px;
    }
  }

  &__item-date {
    font-family: 'Helvetica';
    font-weight: 700;
    font-size: 52px;
    text-transform: uppercase;
    color: #39453B;

    @include medium-desktop {
      margin-right: 20px;
    }
  }
}
